.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 30%;
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 30;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  grid-gap: 50px;
  padding: 20px;
  width: 40%;
  padding-top: 30px;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}